
import { SET_MESSAGE } from '@/store/mutation-types'
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'Snackbar',
  computed: {
    message (): string {
      return this.$store.state.alert.message
    }
  },
  methods: {
    dismiss () {
      this.$store.commit(SET_MESSAGE, '')
    }
  }
})
